import {
  DataQueryRequest,
  DataQueryResponse,
  DataSourceApi,
  DataSourceInstanceSettings,
  MutableDataFrame,
  FieldType,
} from '@grafana/data';

import { HttpQuery, HttpDataSourceOptions } from './types';

export class DataSource extends DataSourceApi<HttpQuery, HttpDataSourceOptions> {
  constructor(instanceSettings: DataSourceInstanceSettings<HttpDataSourceOptions>) {
    super(instanceSettings);
  }

  async query(options: DataQueryRequest<HttpQuery>): Promise<DataQueryResponse> {
    const data = options.targets.map((target) => {
      return new MutableDataFrame({
        refId: target.refId,
        fields: [
          { name: 'message', values: ['This is approval app backend plugin'], type: FieldType.string },
        ],
      });
    });

    return { data };
  }

  async testDatasource() {
    // Implement a health check for your data source.
    return {
      status: 'success',
      message: 'Success',
    };
  }
}
